*,
*::after,
*::before {
  box-sizing: border-box;
}

html, body {
  height: 100%;
}

body {
  height: 100%;
  font-size: 18px;
  font-family: "Montserrat", sans-serif;
  font-weight: 400;
  color: #000000;
  overflow-x: hidden;
}

ul, ol {
  list-style-type: none;
  padding: 0;
}

a {
  display: block;
  font-family: inherit;
  text-decoration: none;
  color: inherit;
  transition: all .3s ease-in-out;
  cursor: pointer;
}

a:hover {
  text-decoration: none;
}

a:active {
  outline: none;
}

a:focus {
  outline: none;
}

input {
  background: transparent;
  border: none;
  outline: none;
  transition: all .3s ease-in-out;
}

input:hover, input:active, input:focus {
  border: none;
  outline: none;
}

button {
  cursor: pointer;
  outline: none;
}

button:active {
  outline: none;
}

button:focus {
  outline: none;
}

img {
  max-width: 100%;
  height: auto;
}

picture {
  display: block;
}

select {
  outline: none;
}

select:focus {
  outline: none;
}

button,
input,
select,
textarea {
  font-family: inherit;
  border: none;
  cursor: pointer;
}

blockquote,
body,
dd,
dl,
figcaption,
figure,
h1,
h2,
h3,
h4,
li,
ol,
p,
ul {
  margin: 0;
}

input:-webkit-autofill,
input:-webkit-autofill:hover,
input:-webkit-autofill:focus,
input:-webkit-autofill,
textarea:-webkit-autofill,
textarea:-webkit-autofill:hover,
textarea:-webkit-autofill:focus,
select:-webkit-autofill,
select:-webkit-autofill:hover,
select:-webkit-autofill:focus {
  border: 0;
  -webkit-text-fill-color: #CCCCCC;
  -webkit-box-shadow: 0 0 0px 1000px transparent inset;
  transition: background-color 5000s ease-in-out 0s;
  background: -webkit-linear-gradient(top, rgba(255, 255, 255, 0) 0%, rgba(0, 174, 255, 0.04) 50%, rgba(255, 255, 255, 0) 51%, rgba(0, 174, 255, 0.03) 100%);
}

.app {
  display: flex;
  flex-direction: column;
}

.content {
  flex: 1 0 auto;
}

.footer {
  flex-shrink: 0;
}

.main {
  margin-top: 94px;
}

@media (max-width: 991px) {
  .main {
    margin-top: 50px;
  }
}

.section {
  position: relative;
}

.section__title {
  margin-bottom: 37px;
  font-size: 30px;
  font-weight: 700;
}

@media (max-width: 768px) {
  .section__title {
    font-size: 20px;
  }
}

.section__title--white {
  color: #FFFFFF;
}

.container {
  max-width: 1140px;
  width: 100%;
  margin: auto;
  padding: 0 30px;
}

@media (max-width: 1024px) {
  .container {
    max-width: 963px;
    padding: 0 20px;
  }
}

@media (max-width: 991px) {
  .container {
    max-width: 100%;
  }
}

.video {
  display: block;
  width: 100%;
  height: 100%;
  background: transparent;
  outline: none;
  border: none;
}

.video:hover {
  outline: none;
}

.video:active {
  outline: none;
}

.video:focus {
  outline: none;
}

.my-bold {
  font-weight: 700;
}

.section-big {
  background: linear-gradient(90.92deg, #07134F 0.68%, #3A0085 99.19%);
}

.btn {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 262px;
  height: 57px;
  background-color: transparent;
  text-align: center;
  font-size: 18px;
  color: #000000;
  border-radius: 28.5px;
  transition: all .3s ease-in-out;
  overflow: hidden;
}

.btn--white {
  background: #FFFFFF;
  filter: drop-shadow(0px 4px 20px rgba(0, 0, 0, 0.15));
}

.btn--white:hover, .btn--white:active, .btn--white:focus {
  background: #5A92FF;
  color: #FFFFFF;
}

.btn--dark {
  background: #FFFFFF;
  border: 1px solid #000000;
}

.btn--dark:hover, .btn--dark:active, .btn--dark:focus {
  background: #5A92FF;
  color: #FFFFFF;
  border-color: transparent;
}

.header-wrapp {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 5;
  background: #FFFFFF;
  box-shadow: 0px 4px 15px rgba(0, 0, 0, 0.1);
}

@media (max-width: 991px) {
  .header-wrapp {
    height: 50px;
  }
}

.header {
  height: 94px;
  display: flex;
  justify-content: space-between;
  align-content: center;
  padding: 20px 0;
}

@media (max-width: 991px) {
  .header {
    height: 50px;
    padding: 10px 0;
  }
}

.header__logo {
  width: 99px;
  height: 53px;
}

@media (max-width: 991px) {
  .header__logo {
    width: 56px;
    height: 30px;
  }
}

.header__img {
  width: 99px;
  height: 53px;
}

@media (max-width: 991px) {
  .header__img {
    width: 56px;
    height: 30px;
  }
}

.nav {
  display: flex;
  align-items: center;
}

@media (max-width: 991px) {
  .nav {
    display: none;
  }
}

.mob-nav {
  display: none;
}

@media (max-width: 991px) {
  .mob-nav {
    position: fixed;
    top: 48px;
    right: -4000px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 20px;
    background: #FFFFFF;
    width: 100%;
    z-index: 5;
  }
  .mob-nav.nav--show {
    right: 0;
  }
}

.nav-list {
  display: flex;
  align-items: center;
  height: 100%;
}

@media (max-width: 991px) {
  .nav-list {
    flex-wrap: wrap;
    height: auto;
  }
}

.nav-list__item {
  margin-left: 43px;
}

@media (max-width: 1024px) {
  .nav-list__item {
    margin-left: 34px;
  }
}

@media (max-width: 991px) {
  .nav-list__item {
    width: 100%;
    margin-left: 0;
    margin-bottom: 20px;
    text-align: center;
  }
}

.nav-list__link {
  color: #000000;
  font-weight: 600;
  font-size: 16px;
  transition: all .3s ease-in-out;
}

.nav-list__link:hover {
  color: #6E68FF;
}

.burger {
  flex-direction: column;
  justify-content: center;
  align-items: flex-end;
  position: absolute;
  top: 18px;
  right: 15px;
  background: transparent;
  width: 21px;
  cursor: pointer;
  z-index: 5;
  display: none;
}

@media (max-width: 991px) {
  .burger {
    display: flex;
  }
}

.burger span {
  display: block;
  width: 16px;
  height: 2px;
  margin-bottom: 6px;
  background: #8A8A8A;
  transition: all .3s ease;
}

.burger span:first-child {
  width: 21px;
}

.burger span:last-child {
  width: 10px;
}

.burger.open {
  top: 24px;
}

.burger.open span {
  width: 70%;
  background: #8A8A8A;
}

.burger.open span:last-child {
  display: none;
}

.burger.open span:first-child {
  transform: rotate(45deg);
  margin-bottom: -1px;
}

.burger.open span:nth-child(2n) {
  transform: rotate(-45deg);
}

.screen-1 {
  padding: 36px 0;
  overflow: hidden;
}

.screen-1::before {
  content: "";
  position: absolute;
  width: 884px;
  height: 884px;
  top: -442px;
  right: -442px;
  background: #5AFFD7;
  border-radius: 50%;
  z-index: -1;
}

@media (min-width: 1920px) {
  .screen-1::before {
    width: 1364px;
    height: 1364px;
    top: -682px;
    right: -682px;
  }
}

@media (max-width: 1024px) {
  .screen-1::before {
    width: 484px;
    height: 484px;
    top: -242px;
    right: -242px;
  }
}

@media (max-width: 768px) {
  .screen-1::before {
    width: 638px;
    height: 638px;
    top: -319px;
    right: -319px;
  }
}

.screen-1::after {
  content: "";
  position: absolute;
  width: 622px;
  height: 622px;
  top: -311px;
  right: -311px;
  background: #6E68FF;
  border-radius: 50%;
  z-index: -1;
}

@media (min-width: 1920px) {
  .screen-1::after {
    width: 958px;
    height: 958px;
    top: -479px;
    right: -479px;
  }
}

@media (max-width: 1024px) {
  .screen-1::after {
    width: 340px;
    height: 340px;
    top: -170px;
    right: -170px;
  }
}

@media (max-width: 768px) {
  .screen-1::after {
    width: 448px;
    height: 448px;
    top: -224px;
    right: -224px;
  }
}

.content-box {
  display: flex;
  justify-content: space-between;
}

@media (max-width: 767px) {
  .content-box {
    flex-wrap: wrap;
  }
}

.content-box__item--video {
  position: relative;
  width: 60%;
  height: 651px;
  background: transparent;
}

.content-box__item--content {
  width: 40%;
  max-width: 390px;
  min-height: 651px;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.content-box__desc {
  font-weight: bold;
  font-size: 32px;
  line-height: 39px;
  margin-bottom: 22px;
}

.content-box__small {
  font-weight: 500;
  font-size: 18px;
  line-height: 26px;
  margin-bottom: 55px;
}

.content-box__text {
  position: relative;
  display: flex;
  align-items: center;
